.beast-seller {
    background-color: $dark-blue;
    height: 600px;

    &__row {
        height: 100%;
        display: flex;
        align-items: flex-end;
    }

    &__col {
        &:first-child {
            height: 100%;
            width: 70%;
            background-image: url('../img/beast-sealer.jpg');
            background-size: cover;
            position: relative;

            &::after {
                content: '';
                display: block;
                background-color: #000;
                opacity: .4;
                position: absolute;
                inset: 0;
            }
        }
    }

    &__info {
        margin-bottom: 20%;
        padding-left: 30px;
    }

    &__buttons {
        display: flex;
        flex-direction: column;

        .btn {
            width: fit-content;
        }

        .btn:first-child {
            margin-bottom: 10px;
        }
    }
}