.product-section {
    padding: 50px 0;

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
    }

    &-top {
        &__btn {
            cursor: pointer;
            border: 0;
            background-color: transparent;
            outline: none;
            padding: 0;

            svg {
                width: 40px;
                height: 40px;
                display: block;
                color: $dark-blue;
                transition: color .2s linear;
            }
        
            &.-prev {
                margin-right: 10px;

                svg {
                    transform: rotate(180deg);
                }
            }

            &:hover {
                svg {
                    color: $red;
                }
            }
        }
    }
}